import back from '@/components/back'

export default {
	components: {
		back
	},
	data() {
		return {
			title: "",
			patient: this.$store.state.patient,
			MerchantId:this.$route.query.MerchantId,
			info:'',
			num:this.$route.query.num,
			page:1,
			list:[],
			haveMore:true,
			activeNum:1,
			scrollTopDistance: 0,
			
			SpecialistHosGroupId:this.$route.query.SpecialistHosGroupId || 0,
			HosGroupName:this.$route.query.HosGroupName || '',
			search:this.$route.query.search,
			from:this.$route.query.from,//1是专科,2是全部
			Goods:[],
			defaultImg: 'this.src="' + require('../../assets/prefRecommend/1.jpg') + '"',
			GoodsNum:0,
			ListDetails:this.$store.state.preList,
			specsShow:false,
			carShow:false,
			spcData:null,
			chooseDrShow:false,
			inputFlag: false,
			pageShow: false,
			showConfirm: false,
			act2:null,
			drSearch:'',
			act3:null,
			user: this.$store.state.userInfo,
			actT:null,
			SpecialId:null,
			idxNum:0,
			GoodsTypeNo:'',
			istHos:false,
			RecommendType:null,
			yEvent:null,
			btnColor:true,
			AreaId:this.$store.state.inspctArea?this.$store.state.inspctArea.AreaId:0,
			AreaName:'',
		}
	},
	computed:{
	    totalPrice(){
	        let p = 0
	        if(this.ListDetails && this.ListDetails.length){
	            this.ListDetails.forEach(v => {
					if(v.GoodsSource == 1){
						 p += (v.Price * v.GoodsNum)
					}else{
						p += (v.VipPrice * v.GoodsNum)
					}
	            })
	        }
	        return p.toFixed(2)
	    },
		totalM(){
			let p = 0
			if(this.ListDetails && this.ListDetails.length){
			    this.ListDetails.forEach(v => {
			        p += Number(v.RecommondAmount) * v.GoodsNum
			    })
			}
			return p
		}
	},
	created() {
		
	},
	mounted() {
		//this.getMore()
	},
	beforeDestroy() {
		window.onscroll = null
	},
	beforeRouteLeave(to, from, next) {
		window.onscroll = null
		if(from.meta.keepAlive) {　
			from.meta.scollTopPosition = this.scrollTopDistance;
		}
		next()
	},
	beforeRouteEnter(to, from, next) { //进入前    	
		next(vm => {
			vm.getMore()
			let flag = false
			vm.ListDetails = vm.$store.state.preList
			if((from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || !vm.$store.state.prevUrl || vm.$store.state.isFresh || vm.MerchantId != vm.$route.query.MerchantId) {
				flag = true
			}
			if(flag) {
				vm.MerchantId = vm.$route.query.MerchantId
				vm.num = vm.$route.query.num
				vm.AreaId = vm.$store.state.inspctArea?vm.$store.state.inspctArea.AreaId:0
				vm.AreaName = vm.$store.state.inspctArea?vm.$store.state.inspctArea.AreaName:''
				vm.page = 1
				vm.haveMore = true
				vm.pull()
				vm.GetMallMerchantGoodsPage()
			} else {
				setTimeout(() => {
					document.documentElement.scrollTop = to.meta.scollTopPosition
				}, 1)
			}
			vm.Contrast()
		});
	},
	methods: {
		sBgHide(){
			this.specsShow = false
			this.carShow = false
			this.chooseDrShow = false
		},
		alertMsgevt(event){
			var $ball = this.$refs.ball;
			$ball.style.top = event.pageY+'px';
			$ball.style.left = event.pageX+'px';
			$ball.style.transition = 'left 0s, top 0s';
			setTimeout(()=>{
				$ball.style.top = (window.innerHeight-50)+'px';
				$ball.style.left = '40px';
				$ball.style.zIndex = 1000;
				$ball.style.transition = 'left 1s linear, top 1s ease-in';
			}, 20)
			setTimeout(()=>{
				$ball.style.display ='none';
				$ball.style.top = window.innerHeight+'px';
				$ball.style.display ='block';
			}, 1000)
		},
		pull() {
			this.$get('PlatFormAPI/SpecialistHosGroup/GetMallMerchantInfoDetail',{
				MerchantId:this.MerchantId
			}).then(o => {
				this.info = o
			})
		},
		GetMallMerchantGoodsPage(h){
			if(this.haveMore){
				this.$post('PlatFormAPI/SpecialistHosGroup/GetMallMerchantGoodsPage',{
					MerchantId:this.MerchantId,
					DrId:this.user.Data.User.DoctorId,
					AreaId:this.AreaId,
					AreaName:this.AreaName,
					PageIndex:this.page,
					PageSize:10
				}).then(o => {
					if (!o.ReturnData || o.ReturnData.length < 10){
						this.haveMore = false
					}
					else {
						this.haveMore = true
					}
					if (h){
						o.ReturnData.forEach(v => {
							this.list.push(v)
						})
					}
					else {
						this.list = o.ReturnData
					}
					this.Contrast()
				})
			}
		},
		Contrast(){
			if(this.ListDetails && this.ListDetails.length){
				this.ListDetails.forEach((v,k) => {
				   this.list.forEach((i,y) => {
					    if(v.GoodsId == i.GoodsId){  
						   this.list.splice(y, 1, v)
					   }
				   })
				})
			}
		},
		tab(aNum){
			this.activeNum = aNum
		},
		add(i,idx,d,$event){
			if(d){
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
						i.GoodsNum = v.GoodsNum + 1
						this.ListDetails.splice(k, 1, i)
					}
				})
				this.alertMsgevt($event)
			}else{
				this.yEvent = $event
				if(i.GoodsSpec && i.GoodsSpec.length > 1 && i.GoodsSource != 1){
					var t = 0
					this.ListDetails.forEach((v,k) => {
						i.GoodsSpec.forEach((a,y) => {
							if(v.GoodsSpecId == a.GoodsSpecId){
							   this.act2 = y
							   t = 1
							}
						})
					})
					if(t==0){
						this.act2 = null
					}
					this.spcData = i
					this.carShow = true
					return
				}else if(i.GoodsSource == 1){
					this.drSearch = ""
					this.spcData = i
					this.chooseDrShow = true
					this.pull()
					return
				}else if(i.GoodsSource == 4){
					if(this.ListDetails && this.ListDetails.length){
						let t = 0
						this.ListDetails.forEach((v,k) => {
						    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
								i.GoodsNum = v.GoodsNum + 1
								this.ListDetails.splice(k, 1, i)
								t = 1
							}
						})
						if(t == 0){
							i.GoodsNum = 1
							this.ListDetails.push(i)
						}
					}else{
						i.GoodsNum = 1
						this.ListDetails.push(i)
					}
					this.alertMsgevt($event)
				}else{
					if(this.ListDetails && this.ListDetails.length){
						let t = 0
						this.ListDetails.forEach((v,k) => {
						    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
								i.GoodsNum = v.GoodsNum + 1
								i.GoodsSpecId = v.GoodsSpecId
								i.GoodsSpecName = v.GoodsSpecName
								this.ListDetails.splice(k, 1, i)
								t = 1
							}
						})
						if(t == 0){
							i.GoodsNum = 1
							i.GoodsSpecId = i.GoodsSpec[0].GoodsSpecId
							i.GoodsSpecName = i.GoodsSpec[0].GoodsSpecName
							this.ListDetails.push(i)
						}
					}else{
						i.GoodsNum = 1
						i.GoodsSpecId = i.GoodsSpec[0].GoodsSpecId
						i.GoodsSpecName = i.GoodsSpec[0].GoodsSpecName
						this.ListDetails.push(i)
					}
					this.alertMsgevt($event)
				}
			}
		
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		addspc(a){
			if(a.GoodsNum){
				this.spcData.GoodsNum = a.GoodsNum + 1
			}else{
				this.spcData.GoodsNum = 1
			}
			this.carShow = false
			this.carShow = true
		},
		reducespc(a){
			if(this.spcData.GoodsNum <= 1){
				return 
			}
			this.spcData.GoodsNum = a.GoodsNum - 1
			this.carShow = false
			this.carShow = true
		},
		sSpecName(i,idx){
			this.act2 = idx
			this.spcData.GoodsSpecId = i.GoodsSpecId
			this.spcData.GoodsSpecName = i.GoodsSpecName
			this.spcData.VipPrice = i.VipPrice
		},
		carSure(a){
			if(!a.GoodsSpecId){
				return this.$toast('请选择规格')
			}
			if(!this.spcData.GoodsNum){
				return this.$toast('请输入数量')
			}
			this.carShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == a.GoodsSource && v.GoodsId == a.GoodsId && v.GoodsClassId == a.GoodsClassId && v.SpecialistGoodsId == a.SpecialistGoodsId){  
						t = 1
						a.GoodsNum = v.GoodsNum
						this.ListDetails.splice(k, 1, a)
					}
				})
				if(t == 0){
					a.GoodsNum = this.spcData.GoodsNum
					this.ListDetails.push(a)
				}
			}else{
				a.GoodsNum = this.spcData.GoodsNum
				this.ListDetails.push(a)
			}
			this.alertMsgevt(this.yEvent)
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		reduce(i,n){
			this.ListDetails.forEach((v,k) => {
			    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){ 
					i.GoodsNum = v.GoodsNum - 1
					if(i.GoodsNum < 1){
						this.ListDetails.splice(k, 1)
					}else{
						this.ListDetails.splice(k, 1, i)
					}
					if(!this.ListDetails.length){
						this.specsShow = false
					}
				}
			})
			console.log(this.ListDetails)
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		specsCar(){
			if(this.specsShow){
				this.specsShow = false
			}else{
				this.specsShow = true
			}
		},
		del(){
			this.specsShow = false
			this.ListDetails = []
			this.list.forEach((v,k) => {
			   v.GoodsNum = 0
			})
			this.$store.dispatch('pushpreList',[])
		},
		closeCar(a){
			this.carShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == a.GoodsSource && v.GoodsId == a.GoodsId && v.GoodsClassId == a.GoodsClassId && v.SpecialistGoodsId == a.SpecialistGoodsId){  
						t = 1
						a.GoodsNum = v.GoodsNum
						this.ListDetails.splice(k, 1, a)
					}
				})
				if(t == 0){
					a.GoodsNum = 0
				}
			}else{
				a.GoodsNum = 0
			}
		},
		sendSave(n){
			if(this.ListDetails && this.ListDetails.length){
				
			}else{
				return this.$toast('请先添加商品')
			}
			this.btnColor = false
			let List = []
			this.ListDetails.forEach(v => {
			    List.push({
					GoodsSource:v.GoodsSource?v.GoodsSource:3,	//商品类型(1-平台服务，2-专科商品，3-平台商品)
					GoodsId:v.GoodsId,	//商品Id/服务项Id
					SpecialistGoodsId:v.GoodsSource == 3 ? 0:v.SpecialistGoodsId,//专科商品Id(商品类型1,2,4 必填，3传0)
					SpecialistHosGroupId:v.SpecialistHosGroupId?v.SpecialistHosGroupId:0, //专科医联体id
					HosGroupName:v.SpecialistHosGroupId?v.HosGroupName:'',  //专科医联体名称
					GoodsName:v.GoodsName,	//商品名称/服务项名称
					GoodsNum:v.GoodsNum,	//商品数量
					DrId:v.DrId || 0,
					DrName:v.DrName||'',
					GoodsSpecId:v.GoodsSpecId || "",	//商品规格Id
					GoodsSpecName:v.GoodsSpecName || "",	//规格名称
			    })
			})
			let url_ = ''
			if(n){
				url_ = 'PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommendUsed'
			}else{
				url_ = 'PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommend'
			}
			this.$post(url_,{
				GoodsRecomId:0,	//商品推荐Id
				DrId:this.user.Data.User.DoctorId,
				DrName:this.user.Data.User.DoctorName,	//医生名称
				GoodsRecomDetail:List,
			}).then(o => {
				if(!o){
					return
				}
				if(n){
					this.$toast("已保存")
					this.btnColor = true
				}else{
					this.$store.dispatch('pushrecomMsg',{
						DataSource:3,
						DataId: o,//数据来源Id
						fromIM:this.$store.state.recomMsg?this.$store.state.recomMsg.fromIM:0
					})
					this.$router.replace(`/prefRecommend/reDetail`)	
				}			
			})
			
		},
		getMore(){
			const el = this.$refs.el
			let timer = null
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop
						|| window.pageYOffset
						|| document.body.scrollTop,
					h = el.scrollHeight,
					wh = window.innerHeight	
					this.scrollTopDistance = top;
					console.log(top)
					if (h - top - wh < 10 && this.haveMore){
                        this.page += 1
						this.GetMallMerchantGoodsPage(true)
					}
				}, 100)
			}
		},
       
	}
}



